import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import { request } from './network/request'
import './style/style.css'
import jquery from "jquery";
Vue.prototype.$ = jquery;

import { Button, Row, Col, Menu, Submenu, MenuItemGroup, MenuItem, Descriptions, DescriptionsItem, 
  Tag, Input, Message, Loading, Form, FormItem, Calendar, Table, TableColumn, Pagination, Card, Dialog, 
  Drawer, Select, Option, MessageBox, TimePicker } from 'element-ui'

Vue.config.productionTip = false

Vue.component(Button.name, Button)
Vue.component(Row.name, Row)
Vue.component(Col.name, Col)
Vue.component(Menu.name, Menu)
Vue.component(Submenu.name, Submenu)
Vue.component(MenuItemGroup.name, MenuItemGroup)
Vue.component(MenuItem.name, MenuItem)
Vue.component(Descriptions.name, Descriptions)
Vue.component(DescriptionsItem.name, DescriptionsItem)
Vue.component(Tag.name, Tag)
Vue.component(Input.name, Input)
Vue.component(Card.name, Card)
Vue.component(Drawer.name, Drawer)
Vue.component(Select.name, Select)
Vue.component(Option.name, Option)
Vue.component(TimePicker.name, TimePicker)

Vue.use(TableColumn)
Vue.use(Table)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Pagination)
Vue.use(Dialog)

Vue.component(Calendar.name, Calendar)
Vue.use(Loading.directive)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

Vue.prototype.$request = request
Vue.prototype.$message = Message
Vue.prototype.$confirm = MessageBox.confirm
